body, html {
  overscroll-behavior: none;
  background-color: #000;
  justify-content: center;
  align-items: center;
  height: 100%;
  margin: 0;
  padding: 0;
  display: flex;
}

canvas {
  background-color: #000;
  width: 100%;
  height: 100%;
  display: block;
}

.container {
  grid-template-rows: auto 1fr auto;
  grid-template-areas: "header"
                       "main"
                       "footer";
  width: 100%;
  height: 100%;
  display: grid;
}

header {
  text-align: center;
  background-color: #f1f1f1;
  border: 1px solid #000;
  grid-area: header;
  font-family: sans-serif;
}

main {
  grid-area: main;
  justify-content: center;
  align-items: center;
  display: flex;
}

#bounds-outer {
  background-color: #e1e1e1;
  width: 100%;
  max-width: 100vmin;
  height: 100%;
  max-height: 100vmin;
}

#bounds-inner {
  box-sizing: border-box;
  background-color: red;
  width: 100vmin;
  height: 100vmin;
}

footer {
  text-align: center;
  background-color: #f1f1f1;
  border: 1px solid #000;
  grid-area: footer;
  font-family: sans-serif;
}

@media (aspect-ratio >= 1) {
  .container {
    grid-template: "header main" 1fr
                   "footer main" 1fr
                   / auto 1fr;
  }

  header, footer {
    writing-mode: vertical-rl;
    text-orientation: mixed;
  }
}

header, footer {
  color: #fff;
  background-color: #000;
}
